@import "../../style/main.scss";

.Button {
    color: $white;
    // padding: 5px 10px 5px 10px;
    border: none;
    border-radius: 5px;
    font: inherit;
    font-weight: 400;
    font-size: inherit;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    width: fit-content;
    padding: auto;

    &:hover {
        box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.4);
    }

    &:focus {
        outline: none;
    }
    &:disabled {
        color: #ccc;
        cursor: not-allowed;
    }

    & .Children {
        display: flex;
        align-items: center;
    }
}
